.playerCard {
  padding: 10px;
  position: relative; }
  .playerCard-wrapper {
    position: relative;
    padding: 10px;
    border-radius: 5px;
    background-color: #f0f0f0;
    box-shadow: 3px 4px 5px 0px rgba(41, 41, 41, 0.25); }
  .playerCard-bigsec {
    border-radius: 5px;
    background-color: white; }
  .playerCard-exit {
    position: absolute;
    top: 10px;
    right: 10px; }
  .playerCard-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  .playerCard-name {
    background: none;
    border: none;
    border-bottom: 1px solid grey;
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    width: 10rem; }
  .playerCard-life {
    text-align: center;
    font-size: 1.5rem;
    background: none;
    border: none;
    border-bottom: 1px solid grey;
    margin-bottom: 10px;
    width: 3rem;
    padding-left: 0.75rem; }

.cmdrDmg {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center; }
  .cmdrDmg-dmg {
    text-align: center;
    font-size: 1rem;
    background: none;
    border: none;
    border-bottom: 1px solid grey;
    width: 2em;
    padding-left: 0.75rem; }
